/* Additional custom styles can be added here */
.h5vp_playlist_container {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 24px;
    background-color: transparent;
    .hidden {
      display: none;
      .preload_poster {
        display: none;
      }
      + .preload_poster {
        display: none;
      }
    }
    .preload_poster { 
      display: block;
      width: 100%;
      aspect-ratio: 16 /9;
    }
  
    video {
      max-width: 100%;
      display: none;
    }
    .plyr {
      width: 100%;
      &.plyr--youtube iframe {
        // position: relative;
        height: 200%;
        top: -50%;
      }
      .plyr__poster {
        background-size: cover;
      }
      .plyr__video-embed iframe {
        display: block;
      }
      .preload_poster {
        display: none;
      }
      + .preload_poster {
        display: none
      }
      video {
        display: block;
      }

    }
    
    .plyr__video-embed {
      width: 100%;
      iframe {
        display: none;
      }
    }
    &.playlist_loaded .video__right {
      aspect-ratio: auto !important;
      padding: 0;
    }
    .video-item {
      padding: 6px;
    }
    .item-active {
      background: #a7a7a7;
      padding: 6px;
      border-radius: 3px;
      // .video-block__title,
      // .video-block__description {
      //   color: #fff !important;

      // }
    }
    .w-100 {
      width: 100%;
    } 
    
    .shimmer {
      overflow: hidden;
      position: relative;
    }
    .shimmer::before {
      content: "";
      position: absolute;
      background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.4) 50%, rgba(255, 255, 255, 0) 100%);
      height: 100%;
      z-index: 1;
      animation: shimmer 1s infinite;
    }
    
    @keyframes shimmer {
      0% {
        width: 0;
      }
      100% {
        width: 100%;
      }
    }
    .faux-image {
      background: #dddddd;
      border-radius: 8px;
      height: 100%;
      position: absolute;
      width: 100%;
    }
    
    .faux-text::before {
      position: absolute;
      content: "";
      width: 100%;
      height: 100%;
      background: #dddddd;
      border-radius: 4px;
    }
    
    .faux-text.short::before {
      width: 60%;
    }
    
    .faux-text.shortage::before {
      width: 40%;
    }
    
    .flex-wrap {
      flex-wrap: wrap;
    }
    
    .mb-1 {
      margin-bottom: 1rem;
    }
    
    body {
      background-color: #a3bbe2;
    }
    
    .video--bg {
      height: 100%;
      width: 100%;
      padding: 30px 60px 30px 60px;
    }
    @media (max-width: 1199px) {
      .video--bg {
        padding: 15px;
      }
    }
    .video__wrapper {
      border-radius: 20px;
      background: linear-gradient(180deg, rgba(255, 255, 255, 0.12) 0%, rgba(255, 255, 255, 0.04) 100%);
      box-shadow: 0px 0px 12px 0px rgba(255, 255, 255, 0.37);
      backdrop-filter: blur(20px);
      padding: 15px;
    }
    .video__left {
      grid-column: span 8;
      // margin-bottom: 30px;
    }
    .video__top {
      grid-column: span 12;
      padding: 0px;
    }
    @media (max-width: 1666px) {
      .video__left {
        grid-column: span 8;
      }
    }
    @media (max-width: 1199px) {
      .video__left {
        grid-column: span 7;
      }
    }
    @media (max-width: 991px) {
      .video__left {
        grid-column: span 12;
      }
      .video__right {
        height: 90vw;
      }
    }
    .video__right {
      display: flex;
      flex-direction: column;
      row-gap: 10px;
      grid-column: span 4;
      margin-bottom: 30px;
      overflow-y: scroll !important;
      overflow-x: hidden !important;
      max-height: 800px;
      aspect-ratio: 4/4.3;
      &::-webkit-scrollbar {
          width: 5px;
      }
      /* Track */
      &::-webkit-scrollbar-track {
          background: #f1f1f1; 
      }
     
      /* Handle */
      &::-webkit-scrollbar-thumb {
          background: #888; 
      }
      
      /* Handle on hover */
      &::-webkit-scrollbar-thumb:hover {
          background: #555; 
      }
    }
    @media (max-width: 1666px) {
      .video__right {
        grid-column: span 4;
      }
    }
    @media (max-width: 1199px) {
      .video__right {
        grid-column: span 5;
      }
    }
    @media (max-width: 991px) {
      .video__right {
        grid-column: span 12;
      }
    }
    .video__thumb {
      border-radius: 10px;
      overflow: hidden;
      display: inline-flex;
      width: 100%;
    }
    .video__thumb--small {
      border-radius: 8px;
      cursor:pointer;
    }
    .video__thumb--small > img {
      width: 100%;
      object-fit: cover;
    }
    .video__container {
      display: flex;
    }
    .video-block {
      display: flex;
      gap: 12px;
      align-items: flex-start;
    }
    .video-block__content {
      display: flex;
      flex-direction: column;
      gap: 2px;
      max-width: 48%;
      margin:5px 0; 
      h2 {
          // line-height: 80%;
          margin-bottom: 0px;
      }
      .video-block__description {
        font-size: 14px;
        line-height: 135%;
        -webkit-line-clamp: 3;
        word-wrap: break-word;
        text-overflow: ellipsis;
        overflow: hidden;
        -webkit-box-orient: vertical;
        display: -webkit-box;
        margin-bottom: 0;
        margin-top: 8px;
      }
    }
    .video-block__container {
      flex-basis: 100%;
      width: 150px;
      max-width: 48%;
      cursor: pointer;
    }
    @media (max-width: 767px) {
      .video-block__container {
        min-width: 100px;
      }
    }
    .video-block__title {
      font-size: 19px;
      color: #000;
      margin-top: 0;
    }
    @media (max-width: 1199px) {
      .video-block__title {
        font-size: 20px;
      }
    }
    @media (max-width: 991px) {
      .video-block__title {
        font-size: 16px;
      }
    }
    .video-block__title > a {
      color: #000;
    }
    .video-item {
      cursor: pointer;
    }
    .video-block__title--small {
      color: #000;
      font-weight: 700;
      font-size: 15px;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      word-wrap: break-word;
      line-height: 135%;
      cursor: pointer;
    }
    @media (max-width: 767px) {
      .video-block__title--small > a {
        -webkit-line-clamp: 1;
        font-size: 13px;
      }
    }
    .video-block .meta {
      display: block;
      color: #787878;
      font-size: 13px;
      line-height: 135%;
    }
    .video__uploader {
      display: flex;
      align-items: self-start;
      gap: 15px;
    }
    .video__uploader__sub {
      font-weight: 700;
    }
    .video__uploader__image {
      max-width: 50px;
      display: flex;
      border-radius: 50%;
      overflow: hidden;
    }
    .video__uploader__image > img {
      width: 100%;
      object-fit: cover;
    }
  }
  
  
