/**
* CSS for video playslist
*/
.h5vp_playlist {
  .simplelist {
    .h5vp_playlist_container {
      gap: 0;
      .video__thumb {
        display: flex;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
      }
    }
    .video__thumb {
      margin: 0;
    }
    li {
      background: #fff;
      color: #222;
      border: 1px solid #dddddd;
      list-style: none;
      position: relative;
      cursor: pointer;
      /* height: 200px;
      position: relative; */
      span.video_thumb {
        width: 100px;
        height: auto;
        display: inline-block;
      }
      img {
        max-width: 100%;
        /* height: 200px;
        object-fit: cover; */
      }
      &.item-active, &:hover, a:focus {
        background: #222;
        color: #fff;
      }
      a {
        color: inherit;
        display: block;
        text-decoration: none;
      }
      h3 {
        margin: 10px;
        font-size: 18px;
        color: inherit
      }
      a div.title {
        font-size: 18px;
      }
      .svg svg {
        fill: currentColor;
        height: 20px;
        width: 20px;
      }
      a svg {
        z-index: 999999;
        position: absolute;
        top: 50%;
        right: 10px;
        transform: translate(0, -50%);
        fill: #00b3ff;
      }
    }
  }
  .plyr__volume input[type="range"] {
    overflow: hidden;
  }

  .plyr__menu [role="menu"] {
    max-height: 250px;
    overflow-y: scroll;
  }
  .plyr {
    .preload_poster {
      display: none;
    }
    + .preload_poster {
      display: none;
    }
  }
}




/* css for listwithposter */
.h5vp_playlist {
  .listwithposter {
    .h5vp_playlist_container  {
      gap: 0;
    }
    .owl-carousel {
     display: flex; 
    }
    .plyr .plyr__poster {
      background-size: cover;
      background-repeat: no-repeat;
    }
    .video-item {
      padding: 0;
    }
    .title {
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      word-wrap: break-word;
      line-height: 135%;
      cursor: pointer;
    }
    .video__thumb  {
      margin-bottom: 0;
      display: block;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
    }
    li span.video_thumb {
      width: 100px;
      height: auto;
      display: inline-block;
    }
    li img {
      max-width: 100%;
      height: 200px;
      object-fit: cover;
      width: 100%;
    }
    li {
      color: #ffffff;
      border: 1px solid #dddddd;
      list-style: none;
      position: relative;
      background: #ffffff;
    }
    li a {
      color: #ffffff;
      /* padding: 5px 20px; */
      display: block;
      text-decoration: none;
    }
    li a .overlay {
      width: 100%;
      height: 200px;
      background: #000000;
      opacity: 0.3;
      position: absolute;
    }
    li a .svg {
      z-index: 999999;
      height: 50px;
      width: 50px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background: #00b3ff;
      border-radius: 50%;
    }
    li a .svg svg {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      height: 20px;
      width: 20px;
      fill: #ffffff;
    }
    li a div.video_title {
      position: absolute;
      bottom: 10px;
      width: 100%;
      text-align: center;
    }
    li {
      .noImage {
        max-width: 100%;
        height: 200px;
        width: 100%;
      }
      h3 {
        margin: 10px;
        font-size: 18px;
        justify-content: center;
        font-weight: 600;
        display: flex;
        align-items: center;
        text-align: center;
      }
      a div.video_title span {
        font-size: 14px;
        top: 10px;
        position: fixed;
        left: 50%;
        height: 100%;
        transform: translate(-50%, 0px);
      }
      a .title {
        text-align: center;
        font-size: 18px;
      }
      &.active a .overlay {
        background: #000000;
        opacity: 0.6;
      }
    }
    .h5vp_playlist_item {
      position: relative;
    }
    &.h5vpNotSlide {
      display: grid;
      grid-template-columns: repeat(3, minmax(150px, 1fr));
      grid-gap: 20px 20px; /* row gap / col gap */
      margin: 20px 0;
       li {
        width: 100%;
      }
    }
    .owl-nav span {
      display: none;
    }
    .owl-nav {
      height: 0;
    }
    .owl-nav .owl-prev:before {
      content: "\2039";
      font-family: "dashicons";
      margin-right: 10px;
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -60%);
      line-height: 1em;
      font-size: 60px;
      color: #fff;
    }
    .owl-nav .owl-prev {
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -60%);
      overflow: hidden;
      width: 23px;
      height: 100%;
    }
    .owl-nav .owl-next:before {
      content: "\203A";
      font-family: "dashicons";
      margin-left: 10px;
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate(0, -60%);
      font-size: 60px;
      line-height: 1em;
      color: #fff;
    }
    .owl-nav .owl-next {
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate(0, -60%);
      overflow: hidden;
      width: 23px;
      height: 100%;
    }
  }


  }
